.greeting {
  .cmp-title__text {
    @include heading-h1($midnight);
  }
  &-mobile {
    display: none;
    @include respond-to(mobile) {
      animation: opacityAnimation 800ms both;
      color: $white;
      display: block;
      margin: 0 !important;
      padding: 1rem;
    }
    &--opacity-animation-out {
      animation: opacityAnimationOut 800ms both;
    }
  }

  @keyframes opacityAnimation {
    0% {
      opacity: 0;
    }
    70% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes opacityAnimationOut {
    0% {
      opacity: 1;
    }
    70% {
      opacity: 0;
    }
    100% {
      opacity: 0;
    }
  }
}
