/* Modal header inner styles - combination base and AEM modal */
%modal-header-styles {

  @include display-flex;
  // justify-content: space-between;
  margin: 0;

  &.-left {
    justify-content: flex-start;
  }
  &.-center {
    justify-content: center;
  }
  &.-right {
    justify-content: flex-end;
  }

  .icon-md, i[class^="icon"] {
    padding: 0;
    position: relative;
    float: left;
    font-size: $large-font-size;
    margin: 4px 5px 4px 0;
    @include media($mobile) {
      font-size: $medium-font-size;
      margin: 0 5px 0 0;
    }
  }

  line-height: 24px;
  // Typically applied to a p element
  .modal-title {
    font-size: 28px;
    line-height: 32px;

    display: block;
    padding-top: 0;
    margin: 0;
    @include media($mobile) {
      font-size: 20px;
      line-height: 24px;
    }
  }
}

/* Button container base styles modal1.5 */
%modal-buttons-base-styles {
  display: block;
  padding: 1em 0 0 0;
  position: relative;
  height: auto;
  width: 100%;
  // Mozilla hack for bottom spacing
  @-moz-document url-prefix() {
    @include media($mobile) {
      margin-bottom: $large-spacing;
    }
  }
  // Allow button order for mobile elements
  @include media($mobile) {
    display: flex;
    flex-wrap: wrap;
    flex-flow: column;
    bottom: 0;
  }

  @include media($tablet-up) {
    display: inline-flex;
    flex-wrap: wrap;
    position: relative;
  }

  &.-left {

    @include media($tablet-up) {
      justify-content: flex-start;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &.-center {
    @include media($tablet-up) {
      justify-content: center;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &.-right {
    .button:last-child {
      margin-right: 0;
    }
    @include media($tablet-up) {
      justify-content: flex-end;
    }
  }

  > div {            
    @include media($mobile) {
      display: flex;
      flex-flow: column;
      position: relative;
      bottom: 0;
      width: 100%;
      margin-bottom: 0;
    }
  }

  .button {
    margin-bottom: 0;
    &:focus {
      @include firefox-tab-focus-buttons;
    }
    // secondary button is moved to the left on desktop
    @include media($tablet-up) {
      &.-inverted, &.modal-btn2 {
        margin-left: 0;
      }
    }

    @include media($device) {
      margin-left: 0;
    }
    @include media($desktop-medium-up) {
      width: auto;
    }
    @include media($device) {
      margin-top: 15px;
    }
    @include media($tablet-up) {
      width: auto;
      margin-right: 0;
      margin-left: $small-spacing;
      padding-left: $small-spacing;
      padding-right: $small-spacing;
    }
    // Old Deprecated styles for KP foundation
    &.-action {
      color: $button-color;
      background-color: white;
      border-color: $button-color;
    }

    &.-action.-inverted {
      color: white;
      background: $button-color;
    }

    &.-action {
      order: 1;
      &.-inverted {
        order: 3;
      }
    }

    // AEM buttons
    &.modal-cancel-btn {
      order: 3;
    }

    &.modal-dont-show-again-btn {
      order: 3;
    }

    &.modal-btn2, &.-inverted { // cancel
      order: 1;
    }

    &.modal-btn1, &.primary, &.-continue { //continue
      order: 2;
    }

    /* Order buttons */
    @include media($mobile) {
     
      // AEM buttons
      &.modal-btn2, &.-inverted {
        order: 2;
      }

      &.modal-btn1, &.primary, &.-continue {
        order: 1;
      }
    }
  }
}

%close-base-styles {
  color: $button-color;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 100;
  
  &:before {
    background-color: white;
  }

  &:focus {
    @include firefox-tab-focus;
  }  
}

%close-base-styles-md {
  padding: $extra-tiny-spacing;
  right: 6px;
  font-size: $medium-font-size;
  @include media($tablet-up) {
    padding: $small-spacing;
    right: 0;
  }
}

%close-base-styles-lg {
  padding: $tiny-spacing $small-spacing;
  font-size: 28px;
}
