$direction: get-direction($layout-direction, $default-layout-direction);


//Span-columns 
@mixin span-columns($span: $columns of $container-columns, $display: block) {
  $columns: nth($span, 1);
  $container-columns: container-span($span);
  $parent-columns: get-parent-columns($container-columns) !global;
  $direction: get-direction($layout-direction, $default-layout-direction);
  $opposite-direction: get-opposite-direction($direction);
  $display-table: is-display-table($container-display-table, $display);

  @if $display-table {
    display: table-cell;
    width: percentage($columns / 12);
  }

  @else {
    float: #{$opposite-direction};

    @if $display !=no-display {
      display: block;
    }

    @if $display==collapse or $display==block-collapse {
      width: flex-grid($columns, $container-columns) + flex-gutter($container-columns);

      &:last-child {
        width: flex-grid($columns, $container-columns);
      }
    }

    @else {
      margin-#{$direction}: flex-gutter($container-columns);
      width: flex-grid($columns, $container-columns);

      &:last-child {
        margin-#{$direction}: 0;
      }
    }
  }
}
//Omega
@mixin omega($query: block, $direction: default) {
  $table: belongs-to(table, $query);
  $auto: belongs-to(auto, $query);
  $direction: get-direction($layout-direction, $default-layout-direction);

  @if length($query)==1 {
    @if $auto {
      &:last-child {
        margin-#{$direction}: 0;
      }
    }

    @else if contains-display-value($query) and $table==false {
      margin-#{$direction}: 0;
    }

    @else {
      @include nth-child($query, $direction);
    }
  }

  @else if length($query)==2 {
    @if $auto {
      &:last-child {
        margin-#{$direction}: 0;
      }
    }

    @else {
      @include nth-child(nth($query, 1), $direction);
    }
  }
}
  

@mixin grid__column($i) {
  @include span-columns($i);

  @if $i from 4 to 6 {
    @include media($mobile) {
      @include fill-parent;
    }
  }

  @if $i == 2 or $i == 3 {
    @include media($mobile) {
      @include span-columns(5.84);
      @include omega(2n);

      margin-#{$direction}: $tiny-spacing;
    }

    @media only screen and (max-width: 400px) {
      width: 46.456406%;
    }
  }

  @if $i == 1 {
    @include media($device) {
      @include span-columns(1);

      @include media($mobile) {
        @include span-columns(2.7);
        margin-#{$direction}: 5.2%;
        @include omega(4n);
      }
    }
  }
}

@mixin grid__container {
  @include clearfix;
  max-width: $desktop-width-max;
  margin-right: $gutter-desktop;
  margin-left: $gutter-desktop;

  @include media($desktop-large) {
    margin-right: auto;
    margin-left: auto;
    padding: 0 $gutter-desktop;
    max-width: $large-desktop-width;
  }

  @include media($desktop-small) {
    margin-right: $gutter-desktop-small;
  }

  @include media($tablet) {
    margin-right: $gutter-tablet;
    margin-left: $gutter-tablet;
  }

  @include media($mobile) {
    margin-right: $gutter-mobile;
    margin-left: $gutter-mobile;
  }
}
