.kp-modal-disable-scroll-ios {
  position: fixed;
  -webkit-overflow-scrolling: touch;
}

.kp-modal-disable-scroll {
  overflow: hidden;
}

.modal-pattern {
  @include kp-modal-pattern; // new modal styles
  display: none;
}

@media print {
  .modal-pattern {
    position: relative;
  }
  .kp-body-component {
    display: none;
  }
}

$columns: 6 8 10;

@each $column in $columns {
  .modal-inner[data-sg-column="#{$column}"] {
    @include grid__column($column);
    float: inherit;
    margin-right: auto;
    &:last-child {
      margin-right: auto; // since it's considered an omega, needed to reset its value
    }
  }
}
