@mixin kp-modal-pattern {
  position: fixed; // Do not remove!
  top: 0;
  left: 0;
  right: 0;
  z-index: 999999;
  height: 100%;

  .modal-showing {
    overflow: hidden;
  }
  &.modal-showing {
    display: block;
    @include media($desktop) {
      overflow: auto;
    }
    .modal-fade-screen {
      opacity: 1;
      visibility: visible;
      @include media($mobile) {
        overflow: hidden;
      }
    }
  }
  &.modal-fullscreen {
    @include kp-modal-fullscreen;
  }

  // Allow this class to be applied to modal-inner or modal-pattern
  &.modal-icons-enabled:not(.modal-fullscreen), &:not(.modal-fullscreen) .modal-icons-enabled {
    .modal-header {

      .modal-icon {
        min-width: 40px;
        display: block;
        i {
          font-size: 30px;
          margin-top: 3px;
        }
        img{
          width: 36px;
          height: 36px;
          position: absolute;
          top: 42px;
          left: 13px;
        }
        @include media($tablet-up) {
          width: 40px;
          i {
            font-size: 40px;
            margin-top: 5px;
          }
          img{
            width: 48px;
            height: 48px;
            position: absolute;
            top: 32.5px;
            left: 36px;
          }
          margin-right: 20px;
        }
      }
    }
    .modal-content {
      .modal-content-body {
        margin-left: 40px;
        @include media($tablet-up) {
          margin-left: 60px;
        }
      }
      .modal-actions {
        @include media($tablet-up) {
          margin-left: 60px;
        }
      }
    }
  }

  .modal-inner {
    position: relative;
    background: white;
    z-index: 999999;
    overflow-y: auto;
    position: relative;

    @include media($mobile) {
      @include full-screen-affect;
      padding: 47px 16px 20px;
      height: 100%;
    }
    @include media($tablet) {
      margin: $gutter-tablet auto 0;
      width: calc(100% - #{$gutter-tablet}); // 12 column grid with tablet gutter // important needed due to sg duplication
      max-height: calc(100% - #{$gutter-tablet * 2});
    }
    @include media($desktop) {
      width: 65.88078%; // 8 columns as default
      margin: 100px auto 100px auto;
      overflow: visible;
    }
    @include media($tablet-up) {
      padding: $large-spacing $xl-spacing $large-spacing $large-spacing;
    }

    & > .-close {
      // modal-extensions.scss
      @extend %close-base-styles;
      @extend %close-base-styles-md;
      &--small{
        // Additional AEM component styles - same as original
        @extend %close-base-styles;
        @extend %close-base-styles-md;
      }
    }

    .modal-header {
      // Combination base and AEM modal styles
      @extend %modal-header-styles;
    }

    .modal-content {
      padding: 0;

      @include media($mobile) {
        @include size(100% auto);
      }
    }

    .modal-buttons {
      @extend %modal-buttons-base-styles;
    }

    // AEM component - .modal-inner > .modal-content > .modal-actions
    .modal-actions {
      .modal-checkboxes {
        padding-bottom: $small-spacing;
        margin: 0 auto;
      }
    }
    .check-box {
      z-index: 1;
    }
  }

  .modal-container {
    @include media($tablet-up) {
      @include newGrid__container;
    }
    @include media($tablet) {
      height: 100%;
      z-index: 999999;
      overflow: hidden;
    }
    @include media($mobile) {
      height: 100%;
      overflow: hidden;
    }
  }
  .modal-fade-screen {
    @include cross-browser('transition', 'opacity 0.4s' $ease-out-cubic);
    @include position(fixed, 0 0 0 0);
    background: rgba(0, 0, 0, 0.65);
    opacity: 0;
    visibility: hidden;
    z-index: 99999;
  }
  @media print {
    .modal-inner {
      margin: 0;
      padding: 0;
      width: 100% !important;
    }
  }
}
