$icon-close-padding-top: 15px;
$icon-top: 40px;
$icon-right: 20px;
$bar-height: 13px;

$mb-icon-close-padding-top: 10px;
$mb-icon-top: 20px;
$mb-icon-right: 10px;
$mb-bar-height: 13px;

$padding-lr-desktop: 38px;
$padding-lr-small-desktop: 35px;
$padding-lr-tablet: 28px;
$padding-lr-mobile: 16px;


@mixin full-screen-affect() {
  width: 100%;
  background: white;
  margin: 0;
  overflow-x: auto;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

@mixin barPositioningMobile($bar: false) {
  @include media($mobile) {
    @if ($bar) {
      padding-top: 90px - 20px;
      margin-bottom: 30px;
    }
    @else {
      padding-top: 23px;
      margin-bottom: 50px;
    }
  }
}

@mixin kp-modal-fullscreen {
  // close icon positioning for items without blue bar
  .modal-container [class^="-close"] {
    @include media($tablet-up){
      font-size: 26px;
      padding: $tiny-spacing $small-spacing;
      top: 25px;
    }
    @include media($mobile){
      font-size: 20px;
      padding: $extra-tiny-spacing;
      right: 6px;
    }

    @include media($desktop-large) {
      right: calc(#{$padding-lr-desktop} - #{$small-spacing});
    }
    @include media($desktop-small-up) {
      right: calc(#{$padding-lr-small-desktop} - #{$small-spacing});
    }
    @include media($tablet) {
      right: calc(#{$padding-lr-tablet} - #{$small-spacing});
    }
    @include media($mobile) {
      right: calc(#{$padding-lr-mobile} - #{$extra-tiny-spacing});
    }
  }

  @each $name, $bgcolor in $colors {
    &.bar_#{$name} {
      .modal-container:before {
        content: '';
        height: 13px;
        display: block;
        background: color($name);
      }
    }
  }

  &.modal-showing {
    .modal-fade-screen {
      opacity: 0;
      visibility: hidden;
    }
  }
  .modal-container {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    background: white;
    z-index: 99;
    max-width: 100%;
    padding: 0;
    margin-left: 0;
    margin-right: 0;

    .modal-inner {
      height: 100%;
      padding-left: 16px;
      padding-right: 16px;
      width: 100%;

      @include media($tablet) {
        padding-bottom: 60px;
        padding-left: 28px;
        padding-right: 28px;
      }
      @include media($desktop) { 
        padding-bottom: 90px;
        padding-left: 38px;
        padding-right: 38px;
      }
      @include media($desktop-small) { 
        padding-left: 35px;
        padding-right: 35px;
      }

      @include media($tablet-up) { //600 + 1px and more
        @include full-screen-affect;
        padding-top: 100px;
        left: 0;
        right: 0;
        max-height: 100%;
      }

      .modal-header {
        @extend %modal-header-styles;
      }
      .modal-buttons {
        @extend %modal-buttons-base-styles;
        @include position(relative, null null null 0);

        input[type="checkbox"] + label {
          order: 1;
          flex: 1 auto;
          margin-right: 0;
          margin-bottom: $small-spacing;
          @include media($desktop) {
            flex: 1;
            margin-bottom: $tiny-spacing;
            float: left;
            display: block;
            width: 57.35098%;
          }
        }
        .button {
          @include media($mobile) {
            width: 100%;
          }
        }
      }
      .modal-content {
        max-width: 1280px;
        margin: 0 auto;
      }
      .check-box {
        z-index: 1;
      }
    }
  }
  // Bar related styles
  &[class*="bar"] {
    .modal-container .modal-inner {
      &:before {
        content: '';
        width: 100%;
        top: 0;
        left: 0;
      }

      @include media($desktop) {
        padding-top: 100px;
        padding-bottom: 90px;
      }
      @include media($tablet) {
        padding-top: 100px;
        padding-bottom: 60px;
      }
      @include media($mobile) {
        padding-top: 90px;
        padding-bottom: 30px;
      }
    }
    [class^="-close"] {

      font-size: 26px;

      @include media($desktop-large) {
        right: calc(#{$padding-lr-desktop} - 20px);
      }
      @include media($desktop-small-up) {
        right: calc(#{$padding-lr-small-desktop} - 20px);
      }
      @include media($tablet) {
        // add desired distance between bar and icon
        // - icon button padding
        top: calc(#{$icon-top} - #{$icon-close-padding-top});
        right: calc(#{$padding-lr-tablet} - 20px);
      }
      @include media($mobile) {
        // add desired distance between bar and icon
        // - icon button padding
        top: calc(#{$mb-icon-top} - #{$mb-icon-close-padding-top});
        right: calc(#{$padding-lr-mobile} - 10px);
      }
    }
  }
}