@import "../variables/image.var.ds2";

@mixin image {
  img {
    width: 100%;
  }
}

@mixin image-border {
  position: relative;
  border: 0;
  border-radius: 4px;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,.12);
    border-radius: 4px;
  }
}

@mixin image-ratio($property) {
    #{$property}: $image__full-width-height--mobile;
    @include media($tablet) { #{$property}: $image__full-width-height--tablet; }
    @include media($desktop) { #{$property}: $image__full-width-height--desktop; }
}

// DS2 IMAGE LOADING STYLES
// for the image loading background color and fade-in
// js does the check for loading and sets '.image-loaded'
// This is intended to be used where the '.image' class
// is used and has a child <img>
// the image fades in at the same speed the background
// color fades out

@mixin image-loading {
  --fade-speed: .2s;

  &:after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: color('blue-xlight');
  }

  &.image-loaded {
    transform: background-color var(--fade-speed) ease-out;

    &:after {
      display: none;
    }

    img {
      opacity: 1;
    }
  }

  img {
    transition: opacity var(--fade-speed) ease-out;
    opacity: 0.1;
  }
}
