.care-display {
  @include respond-to(tablet) {
    display: flex;
    position: relative;
  }
}

.toggle-content {
  display: none;
  height: 0;
  overflow: hidden;
  transition: height 400ms ease-in-out;
}

.toggle-content.is-visible {
  display: block;
  height: auto;
}

.care-display-animation-in {
  animation: primaryCardAnimation 400ms both;
}

@keyframes primaryCardAnimation {
  0% {
    right: 0;
  }
  100% {
    right: calc(100% + 2rem);
  }
}
@include respond-to(tablet) {
  @keyframes primaryCardAnimation {
    0% {
      right: 0;
    }
    100% {
      right: calc(100% + 3.5rem);
    }
  }
}
@include respond-to(desktop) {
  @keyframes primaryCardAnimation {
    0% {
      right: 0;
    }
    100% {
      right: $desktop-right-size-2;
    }
  }
}

.care-display-animation-out {
  animation: primaryCardAnimationOut 400ms both;
}

@keyframes primaryCardAnimationOut {
  0% {
    right: calc(100% + 2rem);
  }
  100% {
    right: 0;
  }
}
@include respond-to(tablet) {
  @keyframes primaryCardAnimationOut {
    0% {
      right: calc(100% + 3.5rem);
    }
    100% {
      right: 0;
    }
  }
}
@include respond-to(desktop) {
  @keyframes primaryCardAnimationOut {
    0% {
      right: $desktop-right-size-2;
    }
    100% {
      right: 0;
    }
  }
}

.care-actions {
  flex: 0 0 100%;
  h3 {
    @include heading-h3();
  }
  .view-toggle,
  .view-toggle-static {
    // TODO: create and use link mixin
    align-items: center;
    background: transparent;
    background-image: url($localImagePath + 'icon-minus.svg');
    background-position: 0.25rem center;
    background-repeat: no-repeat;
    border: 0;
    color: $contrast-blue;
    display: flex;
    font-family: $font-family-heavy;
    font-weight: 500;
    padding: 0.5rem 0.5rem 0.5rem 2rem;
    text-decoration: underline;
    transition: background-color 0.2s ease, color 0.2s ease;
    &:hover {
      background-color: $kp-blue;
      background-image: url($localImagePath + 'icon-minus-hover.svg');
      color: $white;
    }
  }

  .view-toggle-static {
    background-image: url($localImagePath + 'icon-plus.svg');
    &:hover {
      background-image: url($localImagePath + 'icon-plus-hover.svg');
    }
  }
}
.care-actions-primary-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 0 0.75rem;
  padding: 0;
  &--flex,
  &--flex-static {
    display: flex;
    flex: 0 0 100%;
    margin-bottom: 0.75rem;
    @include respond-to(tablet) {
      &--50 {
        flex: 0 0 calc(50% - 1rem);
        &:nth-child(2n - 1) {
          margin-left: 1rem;
          margin-right: 1rem;
        }
      }
      &--33 {
        flex: 0 0 calc(33.3333% - 0.6666rem);
        &:nth-child(3n - 1) {
          margin-left: 1rem;
          margin-right: 1rem;
        }
      }
    }
  }
  &--item {
    background-color: $midnight;
    border: $border;
    border-radius: $border-radius;
    cursor: pointer;
    display: flex;
    flex: 1;
    padding: $padding-md;
    transition: background-color 0.2s ease, color 0.2s ease,
      border-color 0.2s ease;
    &-image {
      margin-right: 1rem;
      img {
        height: 3rem;
        width: 3rem;
      }
    }
    &-content {
      color: $white;
      display: flex;
      flex-direction: column;
    }
    &-title {
      @include respond-to(desktop) {
        font-size: 1.25rem;
        line-height: 1.75rem;
      }
      display: flex;
      font-family: $font-family-heavy;
      font-size: 1rem;
      justify-content: left;
      letter-spacing: -0.01em;
      line-height: 1.5rem;
      margin: 0;
      text-align: left;
      word-break: break-word;
    }
    &-description {
      display: flex;
      font-size: $font-size-xs;
      justify-content: left;
      line-height: 1.25rem;
      margin: 0;
      text-align: left;
      word-break: break-word;
    }
    &-link {
      color: $link-color;
      font-size: $font-size-xs;
      margin: 0;
    }
    &:hover {
      background-color: $contrast-blue;
      color: $white;
      text-decoration: underline;
    }
  }
}

.care-actions-secondary-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 0 0.75rem;
  padding: 0;
  &-button {
    @include button();
    display: inline-block;
    margin: 0 0.625rem 0.625rem 0; // 10px
    text-decoration: none;
  }
}

.care-actions-footer {
  display: inline-block;
  margin: 0;
  &--link {
    // TODO: create and switch to use link mixin
    align-items: flex-end;
    background-color: $white;
    border: 0;
    color: $midnight;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    transition: background-color 0.2s ease, color 0.2s ease,
      border-color 0.2s ease;
    img {
      margin-right: 0.5rem;
    }
    p {
      font-family: $font-family-heavy;
      font-size: $font-size-xs;
      line-height: 1.25rem;
      margin: 0;
      text-decoration: underline;
      @include respond-to(tablet) {
        font-size: $font-size-md;
        line-height: 1.5rem;
      }
    }
    &-number {
      text-decoration: none !important;
    }
    &:hover {
      background-color: $midnight;
      color: $white;
    }
  }
}
